.moviepage {
	max-width: 1440px;
	height: auto;
	margin: 0 auto;
}

.moviepage__container {
	max-width: 1110px;
	display: flex;
	flex-wrap: wrap;
	margin: 50px 165px 0;
	padding-bottom: 83px;
	gap: 53px 28px;
}

.movie-block {
	display: flex;
	gap: 33px;
}

.movie-block__img {
	width: 255px;
	height: 350px;
	border-radius: 10px;
	object-fit: cover;
}

.movie-block__text {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.movie-block__text_title {
	color: var(--white, #FFF);
	font-size: 48px;
	font-family: Oswald;
	font-style: normal;
	font-weight: 600;
	line-height: 56px;
	margin: 0;
}

.moviepage__button__container {
	display: flex;
	flex-direction: column;
	margin: 0 0 0 195px;
	gap: 30px;
	justify-content: space-between;
	align-items: flex-end;
}
