.trailer {
    margin: 0 0 0 165px;
}

.trailer__button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 760px;
    height: 427px;
    border: none;
    cursor: pointer;
    padding: 0;
    border-radius: 10px;
    opacity: 0.6000000238418579;
    position: relative;
}

.add__img {
    position: absolute;
}

.trailer__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
}

.trailer__text {
    color: var(--white, #FFF);
    font-family: Unbounded;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin: 0 0 40px 0;
}
